  display: none;
}
#addonify-quick-view-modal .woocommerce .product .onsale {
  display: inline-flex;
  align-items: center;
  flex-wrap: wrap;
  position: absolute;
  right: auto;
  left: 20px;
  top: 20px;
  bottom: auto;
  z-index: 10;
  margin: 0;
  padding: 5px 15px;
  border: none;
  height: auto;
  width: auto;
