#addonify-quick-view-modal .woocommerce .product .images .woocommerce-product-gallery__wrapper .woocommerce-product-gallery__image--placeholder img {
  display: block;
  width: 100%;
  height: auto;
  padding: 0;
  border: none;
  border-radius: var(--addonify_qv_modal_image_border_radius);
  border: 1px solid var(--addonify_qv_modal_images_border_color);
}
#addonify-quick-view-modal .woocommerce .product .images .flex-control-nav {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  column-gap: 20px;
  row-gap: 20px;
  margin: 0;
  list-style: none;
  padding: 0;
  float: none;
}
#addonify-quick-view-modal .woocommerce .product .images .flex-control-nav::before, #addonify-quick-view-modal .woocommerce .product .images .flex-control-nav::after {
  content: "";
  display: none;
}
#addonify-quick-view-modal .woocommerce .product .images .flex-control-nav li {
  display: block;
  width: 100%;
  max-width: 100%;
  height: auto;
  min-height: 100%;
  margin: 0;
  padding: 0;
  float: none;
  clear: none;
  overflow: hidden;
