  padding: 0;
  border: none;
}
#addonify-quick-view-modal .woocommerce .product .summary {
  display: flex;
  flex-direction: column;
  gap: 20px;
  float: none;
  width: 100%;
  margin: 0;
  padding: 0;
}
#addonify-quick-view-modal .woocommerce .product .summary .summary-content {
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
