/* -------------------------------------------------------------------------- 
	Keyframe - Spin
  -------------------------------------------------------------------------- */
@keyframes AddonifyQuickViewSpin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
/* -------------------------------------------------------------------------- 
	Keyframe - FadeIn
  -------------------------------------------------------------------------- */
@keyframes AddonifyQuickViewFadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
/* -------------------------------------------------------------------------- 
	Keyframe - FadeOut
  -------------------------------------------------------------------------- */
@keyframes AddonifyQuickViewFadeOut {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}
/* -------------------------------------------------------------------------- 
	Keyframe - FadeInUp
  -------------------------------------------------------------------------- */
@keyframes AddonifyQuickViewFadeInUp {
  from {
    opacity: 0;
    transform: translateY(100px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
/* -------------------------------------------------------------------------- 
	Keyframe - FadeOutDown
  -------------------------------------------------------------------------- */
@keyframes AddonifyQuickViewFadeOutDown {
  from {
    opacity: 1;
    transform: translateY(0);
  }
  to {
    opacity: 0;
    transform: translateY(100px);
  }
}
/* -------------------------------------------------------------------------- 
	Keyframe - SlideInLeft
  -------------------------------------------------------------------------- */
@keyframes AddonifyQuickViewSlideInLeft {
  from {
    opacity: 0;
    transform: translateX(-500px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}
/* -------------------------------------------------------------------------- 
	Keyframe - SlideOutLeft
  -------------------------------------------------------------------------- */
@keyframes AddonifyQuickViewSlideOutLeft {
  from {
    opacity: 1;
    transform: translateX(0);
  }
  to {
    opacity: 0;
    transform: translateX(-500px);
  }
}
/* -------------------------------------------------------------------------- 
	Keyframe - SlideInRight
  -------------------------------------------------------------------------- */
@keyframes AddonifyQuickViewSlideInRight {
  from {
    opacity: 0;
    transform: translateX(500px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}
/* -------------------------------------------------------------------------- 
	Keyframe - SlideOutRight
  -------------------------------------------------------------------------- */
@keyframes AddonifyQuickViewSlideOutRight {
  from {
    opacity: 1;
    transform: translateX(0);
  }
  to {
    opacity: 0;
    transform: translateX(500px);
  }
}
/* -------------------------------------------------------------------------- 
	Keyframe - bounceIn
  -------------------------------------------------------------------------- */
@keyframes AddonifyQuickViewBounceIn {
  from, 20%, 40%, 60%, 80%, to {
    animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
  }
  0% {
    opacity: 0;
    transform: scale3d(0.3, 0.3, 0.3);
  }
  20% {
    transform: scale3d(1.1, 1.1, 1.1);
  }
  40% {
    transform: scale3d(0.9, 0.9, 0.9);
  }
  60% {
    opacity: 1;
    transform: scale3d(1.03, 1.03, 1.03);
  }
  80% {
    transform: scale3d(0.97, 0.97, 0.97);
  }
  to {
    opacity: 1;
    transform: scale3d(1, 1, 1);
  }
}
/* -------------------------------------------------------------------------- 
	Keyframe - bounceOut
  -------------------------------------------------------------------------- */
@keyframes AddonifyQuickViewBounceOut {
  20% {
    transform: scale3d(0.9, 0.9, 0.9);
  }
  50%, 55% {
    opacity: 1;
    transform: scale3d(1.1, 1.1, 1.1);
  }
  to {
    opacity: 0;
    transform: scale3d(0.3, 0.3, 0.3);
  }
}
/* -------------------------------------------------------------------------- 
	Keyframe - zoomIn
  -------------------------------------------------------------------------- */
@keyframes AddonifyQuickViewZoomIn {
  from {
    opacity: 0;
    transform: scale3d(0.3, 0.3, 0.3);
  }
  50% {
    opacity: 1;
  }
}
/* -------------------------------------------------------------------------- 
    Keyframe - zoomOut
  -------------------------------------------------------------------------- */
@keyframes AddonifyQuickViewZoomOut {
  from {
    opacity: 1;
  }
  50% {
    opacity: 0;
    transform: scale3d(0.3, 0.3, 0.3);
  }
  to {
    opacity: 0;
  }
}
/* -------------------------------------------------------------------------- 
    Keyframe - Swing
  -------------------------------------------------------------------------- */
@keyframes AddonifyQuickViewSwing {
  20% {
    transform: rotate3d(0, 0, 1, 15deg);
  }
  40% {
    transform: rotate3d(0, 0, 1, -10deg);
  }
  60% {
    transform: rotate3d(0, 0, 1, 5deg);
  }
  80% {
    transform: rotate3d(0, 0, 1, -5deg);
  }
  to {
    transform: rotate3d(0, 0, 1, 0deg);
  }
}
/* -------------------------------------------------------------------------- 
    Keyframe - Jello
  -------------------------------------------------------------------------- */
@keyframes AddonifyQuickViewJello {
  0%, 11.1% {
    transform: none;
  }
  22.2% {
    transform: skewX(-12.5deg) skewY(-12.5deg);
  }
  33.3% {
    transform: skewX(6.25deg) skewY(6.25deg);
  }
  44.4% {
    transform: skewX(-3.125deg) skewY(-3.125deg);
  }
  55.5% {
    transform: skewX(1.5625deg) skewY(1.5625deg);
  }
  66.6% {
    transform: skewX(-0.78125deg) skewY(-0.78125deg);
  }
  77.7% {
    transform: skewX(0.390625deg) skewY(0.390625deg);
  }
  88.8% {
    transform: skewX(-0.1953125deg) skewY(-0.1953125deg);
  }
  to {
    transform: none;
  }
}
/* -------------------------------------------------------------------------- 
	Keyframe - rubberBand
  -------------------------------------------------------------------------- */
@keyframes AddonifyQuickViewRubberBand {
  from {
    transform: scale3d(1, 1, 1);
  }
  30% {
    transform: scale3d(1.25, 0.75, 1);
  }
  40% {
    transform: scale3d(0.75, 1.25, 1);
  }
  50% {
    transform: scale3d(1.15, 0.85, 1);
  }
  65% {
    transform: scale3d(0.95, 1.05, 1);
  }
  75% {
    transform: scale3d(1.05, 0.95, 1);
  }
  to {
    transform: scale3d(1, 1, 1);
  }
}